@import "../../../base.scss";


.menuItem{
    padding: 2rem;
    justify-content: center;
    border: 1px solid $border-color-primary;
    max-height: 320px;

    .imageContainer{
        border-radius: 1rem;
        overflow: hidden;
        max-height: 280px;
    }
    img{
        width: 100%;
        height: 100%;
    }
    .itemInfo{
        margin-top: 0.5rem;
    }
}